import React from "react";
import "../BlogPost.css";
import "./MusicSamplesPost.css";
import { Image, Row, Col } from "react-bootstrap";
import { returnListOfStrings } from "../../functions";
import VideoPlayer from "../VideoPlayer";

const MusicSamplesPost = () => {
  const blogPostJsonFile = require("../../jsonFiles/blogPosts.json");
  const blogPostDataArray = blogPostJsonFile.value;
  const customBlogPosttData = blogPostDataArray.find(
    (entry) => entry.customComponentName === "MusicSamplesPost"
  );
  return (
    <div className="post-container">
      <h1 className="post-title">{customBlogPosttData.title}</h1>
      <p className="post-intro-section">{customBlogPosttData.intro}</p>
      <p className="post-type">
        <i>{returnListOfStrings(customBlogPosttData.type)}</i>
      </p>
      <Row>
        <Col>
          <h2 className="list-entry-title">1. The Alchemist</h2>
        </Col>
      </Row>
      <Row className="justify-content-center mt-3 mb-5">
        <Col md={10}>
          <Image
            src={
              "https://magazine-resources.tidal.com/uploads/2022/10/alc_resized-1280x808.jpg"
            }
            fluid
            alt={"The Alchemist"}
            className="post-image"
            rounded
          ></Image>
        </Col>
      </Row>
      <Row className="justify-content-center">
        <Col md={10}>
          <p className="post-main-text">
            <i>The Alchemist</i> har varit ett tungt namn inom hiphopvrlden en
            bra tid nu. Hans produktion, som ofta r fylld med jazziga, souliga
            och melodiska samples, har gjort honom till en eftertraktad
            producent att arbeta med. <i>Mobb Deep</i>, <i>Action Bronson</i>,
            <i>Freddie Gibbs</i>, <i>Earl Sweatshirt</i> r bara ngra av en
            lng lista av tunga namn som har samarbetat med The Alchemist.
            <br />
            Hans diskografi r fylld med bra projekt, men
            <i>This Thing Of Ours</i> (2021), <i>Bo Jackson</i> (2021) och{" "}
            <i>Alfred</i> (2020) r enligt mig de projekt dr hans produktion
            och sampling skiner starkast. Tre album som jag verkligen inte kan
            rekommendera nog!
          </p>
        </Col>
      </Row>
      <Row>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=vfCjKpbql80"}
            width="100%"
            height="30vh"
          />
        </Col>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=J6QgqZuTWFc"}
            width="100%"
            height="30vh"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <p className="video-description-text">
            Samplet taget frn lten kan hras vid 2:25 och spelas genom hela
            lten.
          </p>
        </Col>
      </Row>
      <Row>
        <Col>
          <h2 className="list-entry-title">2. Jpegmafia</h2>
        </Col>
      </Row>
      <Row className="justify-content-center mt-3 mb-5">
        <Col md={10}>
          <Image
            src={
              "https://www.nme.com/wp-content/uploads/2021/11/JPEGMAFIA_FEAT_2.jpg"
            }
            fluid
            alt={"The Alchemist"}
            className="post-image"
            rounded
          ></Image>
        </Col>
      </Row>
      <Row className="justify-content-center">
        <Col md={10}>
          <p className="post-main-text">
            <i>Jpegmafia</i> kom frst p min radar 2018 med sitt stora
            genombrott med albumet <i>Veteran</i>, och jag kunde verkligen inte
            sluta att imponeras av hans musik. Hans samples r oerhrt obskyra
            och hur lnge han mste leta efter dem r galet. Men det som gr det
            nnu mer imponerande r hur han lyckas integrera dessa samples in i
            sin produktion p ett stt som verkligen fr varje lt att sticka
            ut.
            <br />
            Men ven om jag lskar <i>Veteran</i> och det fick mig att upptcka
            JPEGMAFIA, s r hans samarbete med <i>Danny Brown</i>,{" "}
            <i>Scaring the Hoes</i> frn 2023, hans bsta album utan tvekan. Hr
            r JPEGMAFIA i toppform som rappare och lyckas matcha Danny Browns
            energi och frmgor, men hans frmga som producent r 10/10. Lten{" "}
            <i>Garbage Pale Kids</i> (se nedan) r ett perfekt exempel p
            JPEGMAFIA som producent. Ett sample frn japansk reklam frn
            80-talet som bygger grunden fr ett tungt beat som passar Danny
            Browns speciella rst och flow perfekt.
            <br />
            <br />
            En av mina favoritltar frn 2023, och jag kan inte vnta tills
            deras nsta projekt tillsammans kommer ut!
          </p>
        </Col>
      </Row>
      <Row>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=fnHA9LyF6IA"}
            width="100%"
            height="30vh"
          />
        </Col>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=FNycli3hW88"}
            width="100%"
            height="30vh"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <p className="video-description-text">
            Samplet taget frn reklamfilen kan hras vid 2:28 in i videon och
            spelas genom hela lten.
          </p>
        </Col>
      </Row>
      <Row>
        <Col>
          <h2 className="list-entry-title">3. The Avalanches</h2>
        </Col>
      </Row>
      <Row className="justify-content-center mt-3 mb-5">
        <Col md={10}>
          <Image
            src={
              "https://cached-images.bonnier.news/gcs/bilder/epi-30-dn/UploadedImages/2016/7/5/39bee304-ec41-4a1d-928d-f40a6a3f9a80/bigOriginal.jpg"
            }
            fluid
            alt={"The Alchemist"}
            className="post-image"
            rounded
          ></Image>
        </Col>
      </Row>
      <Row className="justify-content-center">
        <Col md={10}>
          <p className="post-main-text">
            Att beskriva <i>The Avalanches</i> och deras stil r inte det
            lttaste, d deras tre skivor alla har sin unika stil. Men ngot som
            de alla har gemensamt r att byggstenarna till mnga av deras ltar
            r obskyra samples. Och i mnga fall, men speciellt p deras frsta
            skiva <i>Since I Left You</i>, s tar olika samples rollen som
            sngare.
            <br />
            Ett bra exempel p detta r lten <i>Since I Left You</i>, dr de
            har samplat olika delar av sngen frn en lt av{" "}
            <i>The Main Attraction</i>, som sedan loopas genom hela lten p ett
            vldigt kreativt stt. En oerhrt catchy lt som r perfekt att
            cykla till en varm sommardag.
            <br />
            Men lten som jag verkligen vill ge uppmrksamhet till r{" "}
            <i>Frontier Psychiatrist</i> frn samma skiva (se nedan). Ord kan
            inte beskriva, eller tminstone mina ord kan inte beskriva hur
            fantastisk denna lten r och hur kreativt och passande samplet r.
            Lyssna helt enkelt!
          </p>
        </Col>
      </Row>
      <Row>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=qLrnkK2YEcE"}
            width="100%"
            height="30vh"
          />
        </Col>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=sLlprINI-cU"}
            width="100%"
            height="30vh"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <p className="video-description-text">
            The Avalanches har samplat mnga bitar ur en sketch frn Wayne and
            Shusters radioprogram. Ngra av samplesen som hrs genom hela lten
            kan man hra vid 0:14, 0:38, 1:22, 2:44, 3:33, 3:42, 4:00, 4:49 och
            5:25.
          </p>
        </Col>
      </Row>
      <Row>
        <Col>
          <h2 className="list-entry-title">4. Death Grips</h2>
        </Col>
      </Row>
      <Row className="justify-content-center mt-3 mb-5">
        <Col md={10}>
          <Image
            src={
              "https://queenmobs.s3.eu-west-2.amazonaws.com/wp-content/uploads/2015/05/28144411/death-grips.jpg"
            }
            fluid
            alt={"The Alchemist"}
            className="post-image"
            rounded
          ></Image>
        </Col>
      </Row>
      <Row className="justify-content-center">
        <Col md={10}>
          <p className="post-main-text">
            Vet du vilka <i>Death Grips</i> r och r bekant med deras musik s
            behver jag inte sga mycket. Men om du inte har hrt talas om Death
            Grips innan s kan jag kort beskriva deras musik:
            <br />
            "Experimentell hiphop med snabba, aggressiva trummor, rap dr en arg
            <i>MC RIDE</i> skriker in i mikrofonen, och samples som kan vara
            allt frn ett japanskt snabbtg, en gitarr eller hybrisfyllt tal
            frn kultledaren Charles Manson".
            <br />
            <br />
            Death Grips kan vara vldigt svra att komma in i, men nr det vl
            klickar, s r det svrt att uppleva ngot lika speciellt igen.
          </p>
        </Col>
      </Row>
      <Row>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=uiMtCJw_RVc"}
            width="100%"
            height="30vh"
          />
        </Col>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=8_d1vVg36u4"}
            width="100%"
            height="30vh"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <p className="video-description-text">
            Samplet taget frn lten kan hras vid 0:40 och spelas genom hela
            lten.
          </p>
        </Col>
      </Row>
      <Row>
        <Col>
          <h2 className="list-entry-title">5. Madlib</h2>
        </Col>
      </Row>
      <Row className="justify-content-center mt-3 mb-5">
        <Col md={10}>
          <Image
            src={
              "https://culted.com/wp-content/uploads/2022/08/FEATURED-@resident_advisor-scaled.jpg"
            }
            fluid
            alt={"The Alchemist"}
            className="post-image"
            rounded
          ></Image>
        </Col>
      </Row>
      <Row className="justify-content-center">
        <Col md={10}>
          <p className="post-main-text">
            Legendariska producenten <i>Madlib</i>, som ligger bakom ngra av de
            bsta hiphop-albumen ngonsin. <i>Madvillainy</i>, <i>Piata</i> och{" "}
            <i>Shades of Blue</i> r alla projekt som mnga skulle ranka bland
            det bsta som hiphop och rap har att erbjuda! Jag inkluderad d tv
            av dessa r med bland mina topp 10 album ngonsin.
            <br />
            <br />
            Jag kan alldeles fr lite om musik och produktion fr att kunna sga
            exakt vad det r som gr Madlib till en s unik och grym producent.
            r det hans produktion, val av samples, eller val av rappare att
            samarbeta med? Men ngot jag med 100 procent skerhet kan sga r
            att mannen kan producera fantastisk musik som alla borde ge ett
            rligt frsk.
          </p>
        </Col>
      </Row>
      <Row>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=gSJeHDlhYls"}
            width="100%"
            height="30vh"
          />
        </Col>
        <Col md={12} lg={6}>
          <VideoPlayer
            youtubeLink={"https://www.youtube.com/watch?v=lq_2X89pc5I"}
            width="100%"
            height="30vh"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <p className="video-description-text">
            Madlib har samplat flera delar av TV-serien "Ironsides" eftertexter
            (0:00, 2:14 och 2:23) och dessa kan hras genom hela lten.
          </p>
        </Col>
      </Row>
      <Row className="pt-3">
        <Col>
          <p className="post-author-name"> {customBlogPosttData.author}</p>
        </Col>
      </Row>
      <p className="post-time-of-publishing">{customBlogPosttData.timestamp}</p>
    </div>
  );
};

export default MusicSamplesPost;
